import { Component, OnInit } from '@angular/core';
import { ListResultsService } from '../../../services/list-results.service';
import { Job } from '../../../models/job.model';
import { jobInterface } from '../../../interface/job.interface';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-job-list',
    templateUrl: './job-list.component.html',
    styleUrls: ['./job-list.component.css']
})

export class JobListComponent implements OnInit {
    jobs: Job[];
    anumber: number;
    page: number = 1;
    totalPageNumber = 2019;
    retrivedBackData: any;
    
    constructor(private listtingService: ListResultsService, private router: Router, private activatedRoute: ActivatedRoute) {
    }

    ngOnInit() {
        this.activatedRoute.queryParamMap.subscribe(queryParams => {
            console.log('开始检测当前页面的page参数')
            console.log(queryParams);
            console.log(Number(queryParams['params']['page']));
            this.listtingService
                .retriveSearchListing(Number(queryParams['params']['page']))
                .subscribe((response: any) => {
                    console.log(response);
                    this.jobs = response.data;
                    this.totalPageNumber = response.totalPageNumber;
                }, error => {
                    console.log(error);
                });
        })


    }

    receiveCurrentPage($event) {
        this.page = $event;
        this.listtingService
            .retriveSearchListing(this.page)
            .subscribe((response: any) => {
                this.jobs = response.data;
                this.totalPageNumber = response.totalPageNumber;
            })
    }

}
