import { RouterModule, Routes } from "@angular/router";
import { NgModule } from "@angular/core";
import { FrontPageComponent } from "./components/pages/front-page/front-page.component";
import { JobListComponent } from "./components/pages/job-list/job-list.component";
import { SignInFormComponent } from "./components/sub-components/sign-in-form/sign-in-form.component";
import { RegisterFormComponent } from "./components/sub-components/register-form/register-form.component";
import { EditorComponent } from "./components/sub-components/editor/editor.component";
import { JobDescriptionComponent } from "./components/pages/job-description/job-description.component";
import { DashboardComponent } from "./components/pages/dashboard/dashboard.component";
import { Page404Component } from "./components/pages/page404/page404.component";
import { UserAppliedComponent } from "./components/sub-components/user-applied/user-applied.component";
import { UserIntroductionComponent } from "./components/sub-components/user-introduction/user-introduction.component";
import { UserRecommendationsComponent } from "./components/sub-components/user-recommendations/user-recommendations.component";
import { UserResumeComponent } from "./components/sub-components/user-resume/user-resume.component";
import { IntroductionComponent } from "./components/sub-components/introduction/introduction.component";




const routes: Routes = [
  { path: "", component: FrontPageComponent },
  { path: "joblist", component: JobListComponent },
  { path: "signin", component: SignInFormComponent },
  { path: "register", component: RegisterFormComponent },
  { path: "post", component: EditorComponent },
  { path: 'jobdetails/:id', component: JobDescriptionComponent },
  {
    path: 'dashboard/:user', component: DashboardComponent, children: [
      { path: '', redirectTo: 'intro', pathMatch: 'full' },
      { path: 'history', component: UserAppliedComponent },
      { path: 'recommendations', component: UserRecommendationsComponent },
      { path: 'resume', component: UserResumeComponent },
      { path: 'intro', component: UserIntroductionComponent },
    ]
  },
  { path: '404', component: Page404Component },
  { path: '**', redirectTo: '/404' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true },)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
