import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { JobDetailService } from '../../../services/job-detail.service';
import { ActivatedRoute } from '@angular/router';
import { Job } from '../../../models/job.model';
import { UserServiceService } from 'src/app/services/user-service.service';
import { MapServiceService } from 'src/app/services/map-service.service';

//* map imported 
import { AgmMap } from '@agm/core';
import { GoogleMapsAPIWrapper } from '@agm/core/services';

declare var google: any;


@Component({
  selector: 'app-job-description',
  templateUrl: './job-description.component.html',
  styleUrls: ['./job-description.component.css']
})
export class JobDescriptionComponent implements OnInit {

  geocoder: any;
  google: any;

  constructor(
    private _httpClient: HttpClient,
    private jobDescription: JobDetailService,
    private activateRouter: ActivatedRoute,
    private _userService: UserServiceService,
    private _mapService: MapServiceService
  ) { }

  job: Job = new Job();
  jobTitle = '';
  lat: number = 51.678418;
  lng: number = 7.809007;
  jobId = '';
  isButtonDisabled = false;
  isLoading = false;
  isRepeatAlert = false; ß
  isSubmissionAlert = false;

  ngOnInit() {
    this.jobId = this.activateRouter.snapshot.params.id;

    this.jobDescription
      .getJobDescription(this.jobId)
      .subscribe(data => {
        this.job = data
        console.log(this.job);
      });

    // this.findLocation('1114e 7th st, Tuscon,');
    this._mapService
      .getCodeOfAddress('1114E 7 th st tucson, arizona')
      .subscribe(data => {
        if(data.status != 'OK'){
          console.log('shit happens');
        }else{
          this.lat = data.results[0]['geometry']['location'].lat;
          this.lng = data.results[0]['geometry']['location'].lng;
        }
      });

  }


  sendResume() {
    this.isLoading = true;
    let userId = JSON.parse(localStorage.getItem('thekey'));
    console.log(userId)
    setTimeout(() => {
      this._userService
        .jobApply(userId['userid'], this.jobId)
        .subscribe(data => {
          console.log(data);
          if (data.message == 'repeat application') {
            this.isRepeatAlert = true;
            setTimeout(() => {
              this.isRepeatAlert = false;
            }, 2000);
          } else if (data.meg == "apply successfully") {
            console.log('success');
            this.isSubmissionAlert = true;
            setTimeout(() => {
              this.isSubmissionAlert = false;
            }, 2000);
          } else {
            alert('error')
          }

        });
      this.isLoading = false;
    }, 3000);
  }

  alertClose($event) {
    this.isSubmissionAlert = $event;
  }

  repeatClose($event) {
    this.repeatClose = $event;
  }

}
