import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { UserServiceService } from "src/app/services/user-service.service";
import { Router } from "@angular/router";


@Component({
  selector: "app-register-form",
  templateUrl: "./register-form.component.html",
  styleUrls: ["./register-form.component.css"]
})

export class RegisterFormComponent implements OnInit {
  registerForm: FormGroup;
  constructor(
    private fb: FormBuilder,
    private userService: UserServiceService,
    private router: Router,
  ) { }

  retypeCorrect = false;
  alertInfomation = "";
  isAlertDisplay = false;
  loading = false;


  ngOnInit() {
    this.registerForm = this.fb.group({
      email: "",
      password: "",
      retypepassword: "",
      school: "",
      age: "",
    });
  }


  alertWindowClose($event) {
    if (this.alertInfomation == "成功注册，点击关闭跳转至主页") {
      console.log('已经成功注册')
      this.router.navigate(['/']);
    } else {
      this.isAlertDisplay = $event;
    }

  }

  registerSubmit($event) {
    if (
      this.registerForm.value.retypepassword != this.registerForm.value.password
    ) {
      this.retypeCorrect = true;
    } else {
      console.log(this.registerForm.value);
      this.userService
        .userRegister(this.registerForm.value)
        .subscribe(response => {
          console.log(response);
          this.loading = true;
          setTimeout(() => {
            switch (response.state) {
              case 'fail':
              this.loading = false;
                this.alertInfomation = "此邮箱已经注册过";
                this.isAlertDisplay = true;
                break;
              case 'success':
                this.loading = false;
                this.alertInfomation = "成功注册，马上登陆"
                this.isAlertDisplay = true;
                break;
            }
          }, 2000);
        });
    }
  }
}
