import { Component, OnInit } from "@angular/core";
import { AngularEditorConfig } from "@kolkov/angular-editor";
import { PostService } from "../../../services/post.service";

@Component({
  selector: "app-editor",
  templateUrl: "./editor.component.html",
  styleUrls: ["./editor.component.css"]
})
export class EditorComponent implements OnInit {
  constructor(private postService: PostService) {}

  ngOnInit() {}
  isAlertDisplay = false;
  htmlContent = "";
  area = "";
  category = "";
  jobTitle = "";
  fileToUpload: File = null;
  alertSucccessMsg = "成功提交";


  config: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: "30rem",
    minHeight: "5rem",
    placeholder: "请输入工作详情...",
    translate: "no",
    customClasses: [
      {
        name: "quote",
        class: "quote"
      },
      {
        name: "redText",
        class: "redText"
      },
      {
        name: "titleText",
        class: "titleText",
        tag: "h1"
      }
    ]
  };

  onClose($event){
    console.log('capture the changes');
    this.isAlertDisplay = $event;
  }



  handleFileInput(file: FileList) {
    console.log('change function is touched');
    this.fileToUpload = file.item(0);
  }

  postHandler() {
    let formData:FormData = new FormData();
    // formData.append('uploadFile', this.imageFile, this.imageFile.name);
    const newJob = {
      area: this.area,
      category: this.category,
      title: this.jobTitle,
      description: this.htmlContent,
    };

    this.postService.postNewJob(newJob).subscribe(msg=>{
      if(msg.state == 'success'){
        console.log('posting success')
        this.isAlertDisplay = true;
      }
      else{
        console.log('系统错误，请稍后再试');
      }
    });
  }



}
