import { Component, OnInit } from '@angular/core';
import { RouterServiceService } from 'src/app/services/router-service.service';
import { UserServiceService } from 'src/app/services/user-service.service';


@Component({
  selector: 'app-user-recommendations',
  templateUrl: './user-recommendations.component.html',
  styleUrls: ['./user-recommendations.component.css']
})
export class UserRecommendationsComponent implements OnInit {

  constructor(private _userService: UserServiceService, private _routerService: RouterServiceService) { }

  isLoading = false;

  ngOnInit() {
    this.isLoading = true;
    let userId = this._routerService.getTheUserId();
    setTimeout(() => {
      this._userService.getUserInfo('recm').subscribe(content => {
        console.log(content);
        this.isLoading = false;
        console.log('subscribe finished')
      })  
    }, 1000);
  }

}
