import { Component, OnInit } from '@angular/core';
import { UserServiceService } from '../services/user-service.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.css']
})
export class NavBarComponent implements OnInit {

  constructor(
    private userService: UserServiceService,
    private router: Router) {

  }

  isUserLogin = false

  ngOnInit() {
    // navbar 确定登录状态的两种方法，第一是一上来登陆完，第二种是检查是不是有key
    this.userService.getLoginState().subscribe(data => {
      // if(data == true || localStorage.getItem('thekey') != null) this.isUserLogin = true;
      if (data == true || localStorage.getItem('thekey')) this.isUserLogin = true;
      console.log(data == true);
    })

  }

  welcomeRedirect() {
    if (this.isUserLogin == true) {
      let userId = JSON.parse(localStorage.getItem('thekey'));
      console.log(userId);
      this.router.navigate(['dashboard', userId['userid']]);
    } else {
      this.router.navigate(['/signin'])
    }

  }

  registerOrLogout() {
    if (this.isUserLogin == true) {
      this.isUserLogin = false;
      localStorage.removeItem('thekey')
      this.router.navigate(['/'])
    } else {
      console.log('here')
      this.router.navigate(['register'])
      // alert('test')
    }
  }

}
