import { Component, OnInit } from '@angular/core';
import { UserServiceService } from 'src/app/services/user-service.service';
import { RouterServiceService } from 'src/app/services/router-service.service';


@Component({
  selector: 'app-user-resume',
  templateUrl: './user-resume.component.html',
  styleUrls: ['./user-resume.component.css']
})
export class UserResumeComponent implements OnInit {

  constructor(private userService: UserServiceService, private _routerService: RouterServiceService) { }

  isLoading = false;
  resumeAddress = '';

  ngOnInit() {
    this.isLoading = true;
    let userId = this._routerService.getTheUserId();
    setTimeout(() => {
      this.userService.getUserInfo('resume').subscribe(content => {
        console.log(content);
        this.isLoading = false;
        this.resumeAddress = content['resumeAddress']
      }) 
    }, 1000);
  }

}
