import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { ListResultsService } from '../../../services/list-results.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-pages',
    templateUrl: './pages.component.html',
    styleUrls: ['./pages.component.css']
})
export class PagesComponent implements OnInit {
    @Input() totalPageNumber: number;
    @Output() currentPageToParentEvent = new EventEmitter<Number>();
    currentPage: number;
    pageArray = [];
    start: number = 1;
    end: number = 5;

    constructor(private listingResultService: ListResultsService, private router: Router, private activatedRouter: ActivatedRoute) {
    }

    ngOnInit() {
        this.currentPage = 1;
        // TODO: please optimization
        for (let i = this.start; i <= this.end; i++) {
            this.pageArray.push(i);
        }
    }
    
    // for resetting the pageArray 
    setPageArray() {
        let newPageArray = []
        for (let i = this.start; i <= this.end; i++) newPageArray.push(i);
        this.pageArray = newPageArray;
    }

    setPage(clickedPage) {
        // back to the top of the window
        document.documentElement.scrollTop = 0;
        
        this.currentPage = clickedPage;
        if (this.currentPage - 2 <= 1) {
            this.start = 1;
            this.end = this.start + 4
        } else if (this.currentPage + 2 > this.totalPageNumber) {
            this.start = this.totalPageNumber - 4
            this.end = this.totalPageNumber;
        }
        else {
            this.currentPage = this.currentPage;
            this.start = this.currentPage - 2;
            this.end = this.currentPage + 2;
        }

        this.setPageArray();

        // this.currentPageToParentEvent.emit(this.currentPage);
        this.router.navigate([], { relativeTo: this.activatedRouter, queryParams:{page: clickedPage}, queryParamsHandling: "merge"});
    }






}
