import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { UserServiceService } from "src/app/services/user-service.service";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Router } from "@angular/router";




@Component({
  selector: "app-sign-in-form",
  templateUrl: "./sign-in-form.component.html",
  styleUrls: ["./sign-in-form.component.css"]
})
export class SignInFormComponent implements OnInit {
  signInForm: FormGroup;
  constructor(
    private fb: FormBuilder,
    private userService: UserServiceService,
    private http: HttpClient,
    private router: Router,
  ) { }

  ngOnInit() {
    this.signInForm = this.fb.group({
      email: "",
      password: ""
    });
  }

  isNotComplete = false;
  isWrongInfo = false;
  loading = false;
  isAlerting = true;
  alertingContent = '密码有误，请重新尝试';


  signInSubmit($event) {
    // ! check the form first
    if (!this.signInForm.value.email || !this.signInForm.value.password) {
      this.isNotComplete = true;
      return;
    }
    
    this.loading = true;
    event.preventDefault();
    setTimeout(() => {
      const userinformation = this.signInForm.value;
      this.userService.userSignIn(userinformation)
        .subscribe(responseData => {
          console.log(responseData);
          if(responseData.state == 'success'){
            alert('success');
            console.log()
            let theKey = {'token': responseData['token'], 'email':responseData['email']}
            localStorage.setItem('thekey', JSON.stringify(theKey));
            this.router.navigate([`dashboard/${responseData['email:']}`]);
          }else{
            alert('there is something wrong with your password and account');
          }
        });
    }, 3000);




  }
}
