import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import '../env/env';
import { environments } from '../env/env';



@Injectable({
  providedIn: 'root'
})


export class ListResultsService {

  constructor(private httpClient: HttpClient) { }

  retriveSearchListing(page: number) {
    console.log("listing result service");
    return this.httpClient
      .get(`${environments.local}/search?test=yes&hello=helloworld&page=${page}`);
  }

}
