import { Component, OnInit } from '@angular/core';
import { UserServiceService } from 'src/app/services/user-service.service';
import { RouterServiceService } from 'src/app/services/router-service.service';


@Component({
  selector: 'app-user-applied',
  templateUrl: './user-applied.component.html',
  styleUrls: ['./user-applied.component.css']
})
export class UserAppliedComponent implements OnInit {

  constructor(private userService: UserServiceService, private routerService: RouterServiceService,) { }

  isLoading = false;

  jobList = []

  ngOnInit() {
    this.isLoading = true;
    let userId = this.routerService.getTheUserId();
    setTimeout(() => {
      this.userService.getUserInfo('history').subscribe(content => {
        this.jobList = content;
        this.isLoading = false;
        console.log(content);
        
        console.log('subscribe finished')
      })  
    }, 1000);
  }

}
