import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RouterServiceService } from 'src/app/services/router-service.service';
import { UserServiceService } from 'src/app/services/user-service.service';

@Component({
  selector: 'app-user-introduction',
  templateUrl: './user-introduction.component.html',
  styleUrls: ['./user-introduction.component.css']
})
export class UserIntroductionComponent implements OnInit {

  constructor(
    private activateRoute: ActivatedRoute,
    private routerService: RouterServiceService,
    private userService: UserServiceService) {
  }

  isLoading = false;
  selfIntroContent = "";

  ngOnInit() {
    this.isLoading = true;
    let userId = this.routerService.getTheUserId();
    setTimeout(() => {
      this.userService.getUserInfo('intro').subscribe(content => {
        console.log(content);
        this.isLoading = false;
        this.selfIntroContent = content['selfIntro']
      })  
    }, 1000);
  }

  changeLoadingState() {

  }



}
