import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class MapServiceService {
  constructor(private _httpClient: HttpClient) { 

  }

  getCodeOfAddress(address){
    return this._httpClient.get<any>(`https://maps.googleapis.com/maps/api/geocode/json?address=${address}=Winnetka&key=AIzaSyCmeLppnSZHxXtZG4wdFxpce2pnX1p9KA0`);
  }

}
