import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable, BehaviorSubject } from "rxjs";
import { environments } from "../env/env";


@Injectable({
  providedIn: "root"
})
export class UserServiceService {

  private loginState: BehaviorSubject<boolean>;

  constructor(private http: HttpClient) {
    this.loginState = new BehaviorSubject<boolean>(false);
  }

  userSignIn(userInfo): Observable<any> {
    console.log(userInfo);
    return this.http.post("http://localhost:3000/usersignin", userInfo);
  }

  userRegister(userRegisterInfo) {
    const httpOptions = {
      headers: new HttpHeaders({
        "Content-Type": "application/json",
        "Authorization": "secretkey"
      })
    };
    return this.http.post<any>("http://localhost:3000/register", userRegisterInfo, httpOptions);
  }

  userLogIn() {
    this.loginState.next(true);
  }

  userLogout() {
    this.loginState.next(false);
  }

  getLoginState(): Observable<boolean> {
    return this.loginState.asObservable();
  }

  isUserLogin() {
    if (localStorage.getItem('thekey') != null) {
      this.userLogIn();
    }
  }

  getUserInfo(infoCate) {
    console.log(infoCate);
    return this.http.get<any>(`${environments.local}/userInfo?cate=${infoCate}&id=${JSON.parse(localStorage.getItem('thekey'))['email']}`);
  }

  jobApply(userId, postId) {
    console.log(userId, postId);
    return this.http.post<any>(`${environments.local}/userapply`, { postId: postId, userId: userId });
  }

}
